* {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    transition: all 0.1s;
}

::-webkit-scrollbar {
    width: 3px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
    width: 1px;
}

::-webkit-scrollbar-thumb {
    background: #888;
    width: 1px;
}

::-webkit-scrollbar-thumb:hover {
    background: #0832DE;
}

.mobileleft {
    background-color: #363740;
}

.mobileleft_top {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 130px;
}

.mobileleft_top h1 {
    color: #FFF;
    font-family: Inter;
    font-size: 45px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.2px;
}

.mobileleft_profile_logo {
    width: 39px;
    height: 39px;
    border-radius: 39px;
}

.mobileleft_bottom_mobile {
    width: 100%;
    flex-wrap: wrap;
    align-items: center;
    display: flex;
    justify-content: space-between;
}

.mobileleft_link {
    border: 3px solid #363740;
    background-color: #363740;
    outline: none;
    width: 16%;
    height: 56px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.2s ease;
}

.mobilenav-icon {
    width: 50px;
    height: 50px;
    margin-left: 0px;
}

.mobileleft_link_active {
    border-top: 3px solid #DDE2FF;
    border-left: 3px solid #363740;
    border-bottom: 3px solid #DDE2FF;
    border-right: 3px solid #363740;
    background-color: #363740;
    outline: none;
    width: 16%;
    height: 100%;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s ease;
    justify-content: center;
}


.mobileright {
    background-color: #F5F5F9;
}

.mobilenavbar {
    width: 100%;
    height: 100px;
    border-bottom: 1px solid #CFCFCF;
    background: #FFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.mobilenavbar-title {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-left: 5%;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 91.5%;
    display: flex;
    align-items: center;
}

.mobilenavbar-title img {
    width: 32px;
    height: 31px;
    margin-left: 3px;
}

.mobilenavbar div {
    display: flex;
    align-items: center;
    margin-right: 4%;
}

.mobilenavbar div img {
    width: 50px;
    height: 50px;
}

.mobilenavbar input {
    border-radius: 25px;
    border: 2px solid #D1D1D1;
    background: #FFF;
    width: 266px;
    height: 40px;
    margin-right: 10px;
    padding-left: 20px;
    outline: none;
    font-family: Inter;
}

.mobilenavbar input:hover {
    border: 2px solid #DDE2FF;
}


.mobilepatientsstat {
    display: flex;
    justify-content: space-between;
    width: 90%;
    margin-left: 5%;
