.statistics-render {
    overflow: auto;
}

.statistics-top {
    width: 100%;
    display: block;
    height: 50%;
}

.statistics-bottom {
    width: 100%;
    display: block;
    height: 50%;
}

.statistics-top-top h1 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 130%;
    letter-spacing: 0.1px;
    color: #000022;
    margin-left: 25px;
    padding-top: 40px;
}

.statistics-top-top {
    height: 30%;
}

.statistics-top-bottom {
    display: flex;
    height: 70%;
}

.statistics-top-bottom-left {
    width: 360px;
    height: 233px;
    margin-bottom: 10%;
    border: 1px solid #EAEAEA;
    border-radius: 8px;
    margin-left: 25px;
    background-color: #FFFFFF;
}

.statistics-top-bottom-left h1 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
    letter-spacing: 0.01em;
    color: #000000;
    margin-left: 14px;
    margin-top: 20px;
}

.statistics-top-bottom-left h3 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.01em;
    color: #343434;
    margin-left: 14px;
    margin-top: 15px;
}

.statistics-top-bottom-left p {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 130%;
    letter-spacing: 0.1px;
    color: #7D7D7D;
    margin-left: 14px;
    margin-top: 5px;
}

.statistics-top-bottom-left img {
    width: 332px;
    height: 33px;
    margin-top: 18px;
    margin-left: 14px;
}

.statistics-top-bottom-right {
    width: 360px;
    height: 233px;
    margin-bottom: 10%;
    border: 1px solid #EAEAEA;
    border-radius: 8px;
    margin-left: 30px;
    background-color: #FFFFFF;
}

.statistics-top-bottom-right img {
    width: 332px;
    height: 33px;
    margin-top: 18px;
    margin-left: 14px;
}

.statistics-top-bottom-right h1 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 35px;
    line-height: 41px;
    letter-spacing: 0.01em;
    color: #000000;
    margin-left: 14px;
    margin-top: 15px;
}

.statistics-top-bottom-right p {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;
    letter-spacing: 0.1px;
    color: #464646;
    margin-left: 15px;
    margin-top: 9px;
}

.statistics-top-bottom-right button {
    background: #4340DA;
    border-radius: 8px;
    width: 320px;
    margin-left: 14px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 500;
    font-size: 15px;
    line-height: 18px;
    letter-spacing: 0.15px;
    color: #FFFFFF;
    border: 2px solid #4340DA;
    cursor: pointer;
    transition: all 0.3s ease;
    height: 40px;
    margin-top: 19px;
}

.statistics-top-bottom-right button:hover {
    background-color: #FFFFFF;
    color: #4340DA;
}

.statistics-bottom-top h1 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 32px;
    line-height: 130%;
    letter-spacing: 0.1px;
    color: #000022;
    margin-left: 25px;
}

.statistics-bottom-bottom-text {
    display: flex;
    margin-left: 25px;
    margin-top: 32px;
}

.statistics-bottom-bottom-text h1 {
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 700;
    font-size: 32px;
    line-height: 38px;
    letter-spacing: 0.01em;
    color: #323C47;
    width: 152px;
}

.statistics-bottom-bottom-text h1 span {
    font-size: 22px;
}

.statistics-bottom-bottom-diagram {
    height: 30px;
    width: 752px;
    background: #F5F4FF;
    border-radius: 8px;
    margin-left: 25px;
    display: flex;
    margin-top: 20px;
}

.statistics-bottom-bottom-diagram div {
    height: 30px;
    color: white;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 130%;
    letter-spacing: 0.1px;
    display: flex;
    align-items: center;
    transition: all 1s ease-in;
    padding-left: 10px;
}