* {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    transition: all 0.1s;
}

::-webkit-scrollbar {
    width: 3px;
}

::-webkit-scrollbar-track {
    background: #f1f1f1;
    width: 1px;
}

::-webkit-scrollbar-thumb {
    background: #888;
    width: 1px;
}

::-webkit-scrollbar-thumb:hover {
    background: #0832DE;
}

.adminBody {
    display: flex;
}

.left {
    /* background-image: url(https://c4.wallpaperflare.com/wallpaper/696/274/479/gradient-abstract-hd-4k-wallpaper-preview.jpg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center; */
    background-color: #363740;
    width: 235px;
}

.left_top {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    height: 130px;
}

.left_top h1 {
    color: #FFF;
    font-family: Inter;
    font-size: 45px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    letter-spacing: 0.2px;
}

.left_profile_logo {
    width: 39px;
    height: 39px;
    border-radius: 39px;
}

.left_bottom {
    width: 100%;
    flex-wrap: wrap;
    align-items: center;
}

.left_link {
    border: 3px solid #363740;
    background-color: #363740;
    outline: none;
    width: 255px;
    height: 56px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s ease;
}

.left_link h1 {
    margin-left: 12px;
    font-size: 20px;
    color: #A4A6B3;
}

.nav-icon {
    width: 34px;
    height: 34px;
    margin-left: 23px;
}

.left_link:hover {
    border-left: 3px solid #DDE2FF;
    background-color: #3E4049;
}

.left_link:hover h1 {
    color: #DDE2FF;
}

.left_link_active {
    border-top: 3px solid #DDE2FF;
    border-left: 3px solid #363740;
    border-bottom: 3px solid #DDE2FF;
    border-right: 3px solid #363740;
    background-color: #363740;
    outline: none;
    width: 255px;
    height: 56px;
    display: flex;
    align-items: center;
    cursor: pointer;
    transition: all 0.2s ease;
}

.left_link_active h1 {
    margin-left: 12px;
    color: #DDE2FF;
    font-size: 25px;
}


.right {
    background-color: #F5F5F9;
}

.navbar {
    width: 100%;
    height: 100px;
    border-bottom: 1px solid #CFCFCF;
    background: #FFF;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.navbar-title {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-left: 60px;
    font-size: 32px;
    font-style: normal;
    font-weight: 700;
    line-height: 91.5%;
    display: flex;
    align-items: center;
}

.navbar-title img {
    width: 32px;
    height: 31px;
    margin-left: 3px;
}

.navbar div {
    display: flex;
    align-items: center;
}

.navbar div img {
    width: 50px;
    height: 50px;
    margin-right: 60px;
}

.navbar input {
    border-radius: 25px;
    border: 2px solid #D1D1D1;
    background: #FFF;
    width: 266px;
    height: 40px;
    margin-right: 10px;
    padding-left: 20px;
    outline: none;
    font-family: Inter;
}

.navbar input:hover {
    border: 2px solid #DDE2FF;
}